.container{
  display: grid;
  grid-template-areas:
  "logo contacts search items burger";
  grid-template-columns: auto auto 1fr auto  auto;
  grid-column-gap: 1rem;
  grid-template-rows: 1fr;
  
}
.logo{
  grid-area: burger;
}
.contacts{
  grid-area: contacts;
  /* background: red; */
}
.search{
  grid-area: search;
  
}
.items{
  grid-area: items;
  
}
.burger{
  grid-area: logo;
}

@media(max-width: 1000px){
  .container{
    grid-template-areas:
      "contacts contacts contacts contacts"
      "logo search items burger";
    grid-template-columns: auto  1fr auto  auto;
    grid-template-rows: 1rem 4rem;
    
  }
}

